import { Routes, RouterModule } from '@angular/router';  import { CampoDadosComponent }   from './campo_dados.component'; import { CampoDadosDetailsComponent } from './campo_dadosDetails.component'; import { CampoDadosEditComponent } from './campo_dadosEdit.component';  const campo_dados_routes: Routes = [   {     path: '',     component: CampoDadosComponent,     children: [       { path:'details', component: CampoDadosDetailsComponent },       { path:'edit', component: CampoDadosEditComponent }     ]   } ];  export const campo_dados_routing = RouterModule.forChild(campo_dados_routes); 


