import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'campo-dados',   templateUrl: 'campo_dados.component.html' }) export class CampoDadosComponent implements OnInit {       displayMode: CampoDadosDisplayModeEnum;     displayModeEnum = CampoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = CampoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = CampoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = CampoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum CampoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


