import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Campo } from '../../../modelo/Campo'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'campo-dados-details',   templateUrl: 'campo_dadosDetails.component.html' }) export class CampoDadosDetailsComponent implements OnInit {    campo: Campo;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.campo.Campo";                 let id = +params['id'];         this.campo = new Campo();         this.campo.cd_campo = id;         o.objetoJson = this.campo;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a campo. '; }      this.campo = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


